<template>
  <div class="list">
    <el-table :data="tableData" border>
      <el-table-column label="头图" width="200">
        <template slot-scope="scope">
          <img :src="scope.row.img" class="pic"  />
        </template>
      </el-table-column>
      <el-table-column prop="title" label="新闻标题">
        <template slot-scope="scope">
          {{scope.row.title}}
        </template>
      </el-table-column>
      <el-table-column prop="author" label="作者">
        <template slot-scope="scope">
          {{scope.row.author}}
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="edit(scope.row._id)"
            >编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="del(scope.row._id)"
            >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
    export default {
        name: "news_list",
        data() {
          return {
            tableData:[]
          }
        },
        methods: {
          getData() {
            this.$axios.get('getNews').then(res => {
              this.tableData = res.data
            })
          },
          del(id) {
            this.$confirm('是否永久删除这条新闻！！！', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.$axios.post('delNews',{_id:id}).then(res => {
                if(res.code == 200){
                  this.$message({
                    type: 'success',
                    message: '删除成功!'
                  });
                  this.$router.go(0)
                }else{
                  this.$message({
                    type: 'danger',
                    message: '删除失败!'
                  });
                }
              })
              
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '已取消删除'
              });          
            });
          },
          edit(id) {
            this.$router.push({path:"/news/update",query:{id}})
          }
        },
        created() {
          this.getData()
        }
    }
</script>

<style scoped>
.list{
  margin: 20px 20px 0;
}
.pic{
  width: 80px;
  height: 80px;
}
</style>



